<template>
  <div>
    <div v-for="style in styles" :key="style" class="styles-container">
      <div v-if="style.includes(closet[selectedId]) == true" class="styles-container__codi">
        <a class="icon_like" href="#" @click="changeLiked(style)">
          <img v-if="style[4] === 'none'" src="../assets/heart_empty.svg">
          <img v-else src="../assets/heart_filled.svg">
        </a>
        <div class="codi_sets">
          <img v-for="item in style" :key="item" :src="item.url" style="height: 80px"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Codi.vue",
  data(){
    return {
      stylesIndexArr: [],
    }
  },
  props: {
    styles: Array,
    closet: Object,
    selectedId: Number,
  },
  methods: {
    changeLiked(arr){
      // const stylesIndexArr = [];

      if(arr[4] == 'none') {
        arr[4] = 'liked';

        // 인덱스 배열에 저장
        this.stylesIndexArr.push(this.styles.indexOf(arr));
      }
      else {
        arr[4] = 'none';

        // 인덱스 배열에서 찾아서, 삭제
        const findIndex = this.stylesIndexArr.findIndex(v => v == this.styles.indexOf(arr));
        this.stylesIndexArr.splice(findIndex, 1);
      }
        localStorage.setItem('stylesNumber', JSON.stringify(this.stylesIndexArr));      
    }
  },
}
</script>

