<template>
  <div class="liked">
    <h2>즐겨찾는 코디 조합</h2>

    <div class="styles-container">
        <div>
            <div class="styles-container__codi" v-for="stylesNumber in getArr" :key="stylesNumber">
                <!-- <a class="icon_like" href="#" @click="changeLiked(styles[stylesNumber])">
                    <img v-if="styles[stylesNumber][4] === 'none'" src="../assets/heart_empty.svg">
                    <img v-else src="../assets/heart_filled.svg">
                </a> -->
                <div class="codi_sets" >
                    <img :src="styles[stylesNumber][0].url" style="height: 80px"/>
                    <img :src="styles[stylesNumber][1].url" style="height: 80px"/>
                    <img :src="styles[stylesNumber][2].url" style="height: 80px"/>
                    <img :src="styles[stylesNumber][3].url" style="height: 80px"/>
                </div>
            </div>
        </div>
    </div>

  </div>
</template>

<script>
import styles from '../data/styles';

export default {
    data(){
        return {
            styles,
        }
    },
    created(){
    },
    components: {
    },
    computed: {
        getArr(){
            const output = localStorage.getItem('stylesNumber');
            const stylesNumberArr = JSON.parse(output);
            console.log(stylesNumberArr);

            return stylesNumberArr;
        }
    },
    methods: {
        changeLiked(arr){
      // const stylesIndexArr = [];

      if(arr[4] == 'none') {
        arr[4] = 'liked';

        // 인덱스 배열에 저장
        this.stylesIndexArr.push(this.styles.indexOf(arr));
      }
      else {
        arr[4] = 'none';

        // 인덱스 배열에서 찾아서, 삭제
        const findIndex = this.stylesIndexArr.findIndex(v => v == this.styles.indexOf(arr));
        this.stylesIndexArr.splice(findIndex, 1);
      }
        localStorage.setItem('stylesNumber', JSON.stringify(this.stylesIndexArr));      
    }
    }
}
</script>

