<template>
<div class="random">

  <div class="random-select">
    <h2 v-if="click == 0">그래도 귀찮은 신명민을 위한 <br/> 랜덤 패션 뽑기</h2>
    <img class="random-select__img" v-if="click == 0" src="../assets/img_random.png"/>
  </div>

  <div v-if="click > 0" class="random-show">
    <h2>오늘의 스타일 추천</h2>
    <div class="random-show__codi">
      <img v-if="click > 0" :src="this.show[0][0].url">
      <img v-if="click > 0" :src="this.show[0][1].url">
      <img v-if="click > 0" :src="this.show[0][2].url">
      <img v-if="click > 0" :src="this.show[0][3].url">
    </div>
  </div>

  <button class="random__btn" @click="randomStyle(styles)">
    <span v-if="click == 0">랜덤 뽑기</span>
    <span v-if="click > 0">다른 스타일 뽑기</span>
  </button>
</div>
</template>

<script>
import styles from '../data/styles';
export default {
data() {
    return {
      styles,
      show: [],
      click: 0,
    }
  },
  methods: {
    randomStyle(arr){
      const random = Math.floor(Math.random() * arr.length);
    //   console.log(arr[random]);
      this.show.unshift(arr[random]);
    //   console.log(this.show[0][0].url);
      this.click++;
    },
  },

}
</script>

